import { http } from './http_service'; 

export function storeTransaction(transactionData) {
    return http().post('/store_transaction', transactionData);
}

export function loadTransactions() {
    return http().post('/load_transactions');
}

export function loadMeterTransactions(meterId) {
    return http().post('/load_meter_transactions', meterId);
}

export function loadMoreTransactions(lastDate) {
    return http().post('/load_more_transactions', lastDate);
}

export function loadMeterMoreTransactions(lastDate) {
    return http().post('/load_meter_more_transactions', lastDate);
}

