<template>
    <main class="dashboard">
        <section class="page-title-section theme-bg dashboard-title">
            <div class="container dashboard-modified"></div>
        </section>

        <section class="min-80vh">
            <div class="container container-large pt-3">
                <div class="row">
                    <div class="col-md-4">
                        <div class="card">
                            <div class="card-body hover-loadbtn">
                                <img
                                    src="../../assets/static/img/water_drop.svg"
                                    alt="water meter"
                                    class="meter_icon float-left mr-1"
                                >
                                <h5 class="card-title dark-color float-left">Water</h5>
                                <button
                                    v-if="waterMeterNumber.length < 1"
                                    v-on:click="waterMeterCheck = !waterMeterCheck"
                                    class="btn btn-primary btn-sm float-right"
                                    title="Add Meter"
                                >
                                    <span :class="[waterMeterCheck ? 'ti-minus' : 'ti-plus']"></span>
                                </button>
                                <div class="clearfix"></div>

                                <div
                                    v-if="waterMeterCheck || waterMeterNumber.length > 0"
                                    class="subscribe-box m-20px-t"
                                >
                                    <input
                                        type="number"
                                        v-model="waterMeterNumber"
                                        class="form-control no_btns"
                                        placeholder="Enter your meter number"
                                    >
                                    <button
                                        v-on:click="showWaterMeterModal"
                                        class="btn btn-primary btn-min-width"
                                    >
                                        <i class="ti-arrow-right"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="col-md-4">
                        <div class="card">
                            <div class="card-body">
                                <img
                                    src="../../assets/static/img/electricity_flash.svg"
                                    alt="water meter"
                                    class="meter_icon float-left mr-1"
                                >
                                <h5 class="card-title dark-color float-left">Electricity</h5>
                                <button
                                    v-if="electricityMeterNumber.length < 1"
                                    v-on:click="electricityMeterCheck = !electricityMeterCheck"
                                    class="btn btn-primary btn-sm float-right"
                                    title="Add Meter"
                                >
                                    <span :class="[electricityMeterCheck ? 'ti-minus' : 'ti-plus']"></span>
                                </button>
                                <div class="clearfix"></div>

                                <div
                                    v-if="electricityMeterCheck || electricityMeterNumber.length > 0"
                                    class="subscribe-box m-20px-t"
                                >
                                    <input
                                        type="number"
                                        v-model="electricityMeterNumber"
                                        class="form-control no_btns"
                                        placeholder="Enter your meter number"
                                    >
                                    <button
                                        v-on:click="showElectricityMeterModal"
                                        class="btn btn-primary btn-min-width"
                                    >
                                        <i class="ti-arrow-right"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="col-md-4">
                        <div class="card">
                            <div class="card-body">
                                <img
                                    src="../../assets/static/img/gas_fire.svg"
                                    alt="water meter"
                                    class="meter_icon float-left mr-1"
                                >
                                <h5 class="card-title dark-color float-left">Gas</h5>
                                <button
                                    v-if="gasMeterNumber.length < 1"
                                    v-on:click="commingSoon"
                                    class="btn btn-primary btn-sm float-right"
                                    title="Add Meter"
                                >
                                    <span :class="[gasMeterCheck ? 'ti-minus' : 'ti-plus']"></span>
                                </button>
                                <div class="clearfix"></div>

                                <div
                                    v-if="gasMeterCheck || gasMeterNumber.length > 0"
                                    class="subscribe-box m-20px-t"
                                >
                                    <input
                                        type="number"
                                        v-model="gasMeterNumber"
                                        class="form-control no_btns"
                                        placeholder="Enter your meter number"
                                    >
                                    <button
                                        v-on:click="showGasMeterModal"
                                        class="btn btn-primary btn-min-width"
                                    >
                                        <i class="ti-arrow-right"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="clearfix"></div>
                    <div class="col-md-12 mt-5" v-if="meterTransactions.meters && meterTransactions.meters.length > 0">
                        <div class="row">
                            <div class="col-md-6">
                                <h5 class="dark-color">Transactions</h5>
                            </div>
                            <div class="col-md-6 text-right">
                                <button v-for="(meter, index) in meterTransactions.meters" :key="index" v-on:click="loadMeterTransactions(meter._id);" :class="[meter._id === selectedMeter ? 'btn-warning' : 'btn-primary']" class="btn btn-sm ml-2" >
                                    <span class="ti-arrow-down"></span> {{meter.meterType}}
                                </button>
                                <button v-if="meterTransactions.transactions && meterTransactions.transactions.length > 0" v-on:click="loadTransactions()" :class="[selectedMeter === '' ? 'btn-warning' : 'btn-primary']" class="btn btn-sm ml-2">
                                    <span class="ti-arrow-down"></span> All
                                </button>
                                
                            </div>
                        </div>
                        
                        <table class="table">
                            <thead>
                                <tr>
                                    <th scope="col">Meter#</th>
                                    <th scope="col">Amount</th>
                                    <th scope="col">Token</th>
                                    <th scope="col">Date</th>
                                    <th scope="col">Type</th>
                                    <th scope="col">Reward <span class="crunchbase-tooltip-indicator" title="Join vendr utility pro to claim points" ></span></th>
                                </tr>
                            </thead>
                            <tbody v-if="meterTransactions.transactions && meterTransactions.transactions.length > 0">
                                <tr  v-for="(transaction, index) in meterTransactions.transactions" :key="index">
                                    <th>
                                        <img v-if="transaction.meterId.meterType === 'water'" src="../../assets/static/img/water_drop.svg" alt="water meter" class="meter_icon " />
                                        <img v-if="transaction.meterId.meterType === 'electricity'" src="../../assets/static/img/electricity_flash.svg" alt="water meter" class="meter_icon " />
                                        <img v-if="transaction.meterId.meterType === 'gas'" src="../../assets/static/img/gas_fire.svg" alt="water meter" class="meter_icon " />

                                        {{transaction.meterNumber}} 
                                    </th>
                                    <td>{{transaction.amount}}</td>
                                    <td>{{transaction.tokenNumber}}</td>
                                    <td>{{transaction.createdAt | date}}</td>
                                    <td>{{transaction.transactionType}}</td>
                                    <td>0.00</td>
                                </tr>
                            </tbody>
                            <tbody v-else>
                                <tr>
                                    <td colspan="5">You have not done any transaction yet!</td>
                                </tr>
                            </tbody>
                        </table>

                        <div class="row" v-if="moreExists === true">
                            <div class="col-md-12 text-center">
                                <button v-if="selectedMeter.length > 0 && meterTransactions.transactions.length > 4" class="btn btn-primary btn-sm" v-on:click="loadMeterMoreTransactions"> <span class="ti-arrow-down"></span> Load More m</button>
                                <button v-if="selectedMeter.length < 1 && meterTransactions.transactions.length > 4" class="btn btn-primary btn-sm" v-on:click="loadMoreTransactions"> <span class="ti-arrow-down"></span> Load More</button>
                            </div>
                        </div>
                        <br/>
                        
                    </div>
                    <div class="col-md-12 mt-5" v-else><h3 class="text-center dark-color">You have not added any meter yet!</h3></div>
                </div>
            </div>

            <b-modal ref="waterMeterModal" hide-footer hide-header size="xl">
                <h4 class="dark-gray-color">Top up water Meter Number: {{waterMeterNumber}}</h4>
                <hr>

                <form v-on:submit.prevent="handlePaymentRequest('water', waterMeterNumber)">
                    <div v-if="!validateFirstStepCheckWater">
                        <ul class="price_list mb-0">
                            <li>Meter Installment: &#8358; 500</li>
                            <li>Reconnection Fee: &#8358; 1500</li>
                            <li class="pb-0">
                                <span class="float-left mr-2">Enter Purchase Amount: </span>  
                                <div class="input-group modified">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="basic-addon1">&#8358;</span>
                                    </div>
                                    <input
                                        type="number"
                                        class="form-control no_btns font-18px"
                                        placeholder="Amount"
                                        v-model="amountToPay" />
                                </div>
                            </li>
                            <div class="clearfix"></div>
                            <div class="invalid-feedback block text-center">{{errors.amountToPay}}</div>
                        </ul>
                    </div>
                    
                    <div v-else>
                        <ul class="price_list">
                            <li>Meter Installment: &#8358; 500</li>
                            <li>Reconnection Fee: &#8358; 1500</li>
                            <li>Service Fee: 100</li>
                            <li>Top up: &#8358; {{amountToPay}}</li>
                            <li>Total: &#8358; {{totalAmount}}</li>
                        </ul>
                    </div>

                    <footer class="modal-footer mt-2 pb-0 justify-content-center">
                        <button type="button" class="btn btn-secondary " v-on:click="hideWaterMeterModal">Close</button>
                        <a v-if="!validateFirstStepCheckWater" v-on:click="validateFirstStepWater()" class="btn btn-primary pointer white-color ">
                            <span class="ti-arrow-top-right"></span> Top up
                        </a>
                        <a v-else v-on:click="validateFirstStepCheckWater = false"  class="btn btn-primary pointer white-color ">
                            <span class="ti-arrow-left"></span> Back
                        </a>
                        <button v-if="validateFirstStepCheckWater" type="submit" class="btn btn-primary payButton ">
                            <span class="ti-check"></span> Proceed to payment
                        </button>
                    </footer>
                    <p v-if="!validateFirstStepCheckWater" class="text-center mt-2 mb-0">You won't be charged yet</p>
                </form>
            </b-modal>

            <b-modal ref="electricityMeterModal" hide-footer hide-header size="xl">
                <h4 class="dark-gray-color">Top up electricity Meter Number: {{electricityMeterNumber}}</h4>
                <hr>
                <form v-on:submit.prevent="handlePaymentRequest('electricity', electricityMeterNumber)">
                    <div v-if="!validateFirstStepCheckElectricity">
                        <ul class="price_list mb-0">
                            <li>Gardening: &#8358; 500</li>
                            <li>Estate Security: &#8358; 1500</li>
                            <li class="pb-0">
                                <span class="float-left mr-2">Enter Purchase Amount: </span>  
                                <div class="input-group modified">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="basic-addon1">&#8358;</span>
                                    </div>
                                    <input
                                        type="number"
                                        class="form-control no_btns font-18px"
                                        placeholder="Amount"
                                        v-model="amountToPay" />
                                </div>
                            </li>
                            <div class="clearfix"></div>
                            <div class="invalid-feedback block text-center">{{errors.amountToPay}}</div>
                        </ul>
                    </div>

                    <div v-else>
                        <div class="row">
                            <div class="col-md-6">
                                <ul class="price_list float-left">
                                    <li>Gardening: &#8358; 500</li>
                                    <li>Estate Security: &#8358; 1500</li>
                                    <li>Convenience Fee: &#8358; 100</li>
                                    <li>Top up: &#8358; {{amountToPay}}</li>
                                    <li>Total: &#8358; {{totalAmount}}</li>
                                </ul>
                            </div>
                            <div class="col-md-6">
                                <ul class="mb-0 alert alert-warning text-left list-unstyled" v-if="electricityMeterOwnerInfo.meterNumber">
                                    <li>Customer Name: {{electricityMeterOwnerInfo.customerName}}</li>
                                    <li>Mobile Number: {{electricityMeterOwnerInfo.mobileNumber}}</li>
                                    <li>Meter Number: {{electricityMeterOwnerInfo.meterNumber}}</li>
                                    <li>Meter Type: {{electricityMeterOwnerInfo.meterType}}</li>
                                    <li>Disco Type: {{electricityMeterOwnerInfo.discoType}}</li>
                                    <li>Minumum Purchase: {{electricityMeterOwnerInfo.minimumPurchase}}</li>
                                    <li>Reference Number: {{electricityMeterOwnerInfo.referenceNumber}}</li>
                                    <li>Customer Address: {{electricityMeterOwnerInfo.customerAddress}}</li>
                                </ul>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                    </div>

                    
                    <footer class="modal-footer mt-2 pb-0 justify-content-center">
                        <button type="button" class="btn btn-secondary " v-on:click="hideElectricityMeterModal">Close</button>
                        <button type="button" v-if="!validateFirstStepCheckElectricity" v-on:click="validateFirstStepElectricity()" class="btn btn-primary pointer white-color " :disabled="electricityMeterVerification">
                            <span v-if="electricityMeterVerification">
                                 <span class="fa fa-spinner fa-spin"></span> Verifying your meter
                            </span> 
                            <span v-else>
                                <span class="ti-arrow-top-right"></span> Top up 
                            </span>
                        </button>
                        <a v-else v-on:click="validateFirstStepCheckElectricity = false"  class="btn btn-primary pointer white-color ">
                            <span class="ti-arrow-left"></span> Back
                        </a>
                        <button v-if="validateFirstStepCheckElectricity" type="submit" class="btn btn-primary payButton ">
                            <span class="ti-check"></span> Proceed to payment
                        </button>
                    </footer>
                    <p v-if="!validateFirstStepCheckElectricity" class="text-center mt-2 mb-0">You won't be charged yet</p>
                </form>
            </b-modal>

            <b-modal ref="gasMeterModal" hide-footer hide-header>
                <h6>Top up gas</h6>
                <hr>
                <h6>Meter Number: {{gasMeterNumber}}</h6>
                <ul>
                    <li>Gardening: 500</li>
                    <li>Estate Security: 1500</li>
                </ul>
                <form>
                    <div class="d-block text-center">
                        <input
                            type="number"
                            required
                            class="form-control no_btns"
                            placeholder="Enter Amount"
                        >
                    </div>
                    <footer class="modal-footer pb-0 mt-3">
                        <button
                            type="button"
                            class="btn btn-secondary"
                            v-on:click="hideGasMeterModal"
                        >Close</button>
                        <button type="submit" class="btn btn-primary">
                            <span class="ti-check"></span> Pay
                        </button>
                    </footer>
                </form>
            </b-modal>
        </section>
        
        <PayStack 
            :amount="Number(payStackData.amount)"
            :email="payStackData.email"
            :paystackkey="payStackData.paystackkey"
            :reference="payStackData.reference"
            :callback="callback"
            :close="close"
            :embed="false"
            v-on:hidePopUp="hideAllPopups"
        >
        </PayStack> 

    </main>
</template>

<script>
    import * as auth from "../../services/auth_service";
    import * as meter from '../../services/meter_service';
    import * as transaction from '../../services/transaction_service';
    import * as sendSMS from "../../services/sms_service";

    import md5 from 'md5';
    import moment from 'moment';
    import PayStack from '@/components/PayStack.vue';

    export default {
        name: "dashboard",
        components: {
            PayStack
        },
        data() {
            return {
                waterMeterCheck: true,
                electricityMeterCheck: true,
                gasMeterCheck: true,
                waterMeterNumber: '',
                electricityMeterNumber: '',
                gasMeterNumber: '',

                // ids
                waterMeterId: '',
                electricityMeterId: '',
                gasMeterId: '',

                amountToPay: '',
                meterType: '',
                meterNumber: '',
                totalAmount: '',
                totalAmountToPay: Number,
                phoneNumber:this.$store.state.userPhoneNumber,

                // paystack stuffs
                payStackData : {
                    paystackkey: "pk_test_8cc88893ea0334eb8432a399851134a919f6907f",
                    email: '', 
                    amount: Number, // in kobo
                    reference: '',
                },

                errors: [],
                validateFirstStepCheckWater: false,
                validateFirstStepCheckElectricity: false,
                electricityMeterVerification: false,

                meterTransactions: [],
                lastTransactionDate: '',
                moreExists: true,
                electricityMeterOwnerInfo: [], 
                smsMessageData:{
                    tokenData:'',
                    phoneNumber:''
                },
                
                selectedMeter: ''
            };
        },
        created: async function() {
            try {
                const {data} = await meter.getOwnerMeters();

                data.meters.forEach(meter => {
                    if (meter.meterType === 'water') {
                        this.waterMeterNumber =  meter.meterNumber;
                        this.waterMeterId = meter._id;
                    } else {
                        this.waterMeterCheck = false;
                    }
                    if (meter.meterType === 'electricity') {
                        this.electricityMeterNumber = meter.meterNumber;
                        this.electricityMeterId = meter._id;
                    } else {
                        this.electricityMeterCheck = false;
                    }
                    if (meter.meterType === 'gas') {
                        this.gasMeterNumber = meter.meterNumber;
                        this.gasMeterId = meter._id;
                    }  else {
                        this.gasMeterCheck = false;
                    }
                });

                 //const{userData} = await meter.getLoggedInUser();

                this.loadTransactions();
            } catch(error) {
                this.flashMessage.error({
                    title: 'Error fetching data',
                    message: 'Some error occured during fetching your data please refresh the page!',
                    time: 5000,
                    icon: '/img/user-dummy.52069854.jpg'
                });
            }

            
        },
        methods: {
            showWaterMeterModal() {
                
                if (this.waterMeterNumber.length > 0) {
                    this.$refs.waterMeterModal.show();
                    
                }
                
            },
            hideWaterMeterModal() {
                this.$refs.waterMeterModal.hide();
            },
            showElectricityMeterModal() {
                if (this.electricityMeterNumber.length > 0) {
                    this.$refs.electricityMeterModal.show();
                }
            },
            hideElectricityMeterModal() {
                this.$refs.electricityMeterModal.hide();
            },
            showGasMeterModal() {
                if (this.gasMeterNumber.length > 0) {
                    this.$refs.gasMeterModal.show();
                }
            },
            hideGasMeterModal() {
                this.$refs.gasMeterModal.hide();
            },
            hideAllPopups() {
                this.hideWaterMeterModal();
                this.hideGasMeterModal();
                this.hideElectricityMeterModal();
            },
            validateFirstStepWater() {
                this.errors = [];
                const amount = parseInt(this.amountToPay);
                if (isNaN(amount) || amount < 1000) {
                    this.errors.amountToPay = 'You should enter at least 1000 amount';
                    return;
                }
                this.totalAmount = 500+1500+Number(this.amountToPay)+100;
                this.totalAmountToPay = (500+1500+Number(this.amountToPay)+100)*100;
                this.validateFirstStepCheckWater = true;
            },
            validateFirstStepElectricity: async function() {
                this.errors = [];
                const amount = parseInt(this.amountToPay);
                
                if (isNaN(amount) || amount < 1000) {
                    this.errors.amountToPay = 'You should enter at least 1000 amount';
                    return;
                }

                this.electricityMeterVerification = true;
                try {
                    const electricityMeterData = {
                        credentials: {
                            "username": "jomesili", 
                            "secret": "3YNjr9WFmuqh5tyfvg9RDGFCHli9vCRVAavDfskrAH4P3DY809R0qUNOvpRpHbvQbs3Fy" 
                        },
                        meterData: { 
                            "meterNumber": this.electricityMeterNumber, 
                            "meterType": "prepaid", 
                            "mobileNumber":"080123456000", 
                            "amount": this.amountToPay, 
                            "referenceNumber": "123456", 
                            "disco": "kedco"
                        }
                    };

                    const {data} = await meter.verifyElectrictyMeter(electricityMeterData);
                    this.electricityMeterOwnerInfo = data.meterInfo.data;

                    this.totalAmount = 500+1500+Number(this.amountToPay)+100;
                    this.totalAmountToPay = (500+1500+Number(this.amountToPay)+100)*100;
                    this.validateFirstStepCheckElectricity = true;
                    this.electricityMeterVerification = false;
                } catch (error) {
                    this.electricityMeterVerification = false;
                    this.flashMessage.error({
                        title: 'Meter verification failed',
                        message: 'Kindly check your meter number!',
                        time: 5000,
                        icon: '../../assets/static/img/water_drop.svg'
                    });
                }
            },
            handlePaymentRequest(meterType, meterNumber) {
                this.meterType = meterType;
                this.meterNumber = meterNumber;
                this.errors = [];
                const amount = parseInt(this.amountToPay);
                if (isNaN(amount) || amount < 1000 || this.validateFirstStepCheck === false) {
                    this.errors.amountToPay = 'You should enter at least 1000 amount';
                    return;
                }

                this.payStackData.amount = this.totalAmountToPay;
                this.payStackData.email = this.$store.state.userEmail;
                this.$root.$emit('openPayStack');
                
            },
            generateReference() {
                let ref = "";
                let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
        
                for(let i=0; i < 10; i++) {
                    ref += possible.charAt(Math.floor(Math.random() * possible.length));
                }
        
                this.payStackData.reference = ref;
            },
            callback: async function(response){
                if (this.meterType === 'water') {
                    const serial_id = '0000001';
                    const user_id = 'DCESN';
                    const meter_id = this.meterNumber;
                    const token_type = 'CreditToken';
                    const amount = '1.0';
                    const key = '399f0d868b264474a2ad093d7ef530f7';
                    const timestamp = moment().unix();
                    const ciphertext = md5(String(serial_id) + user_id + meter_id + token_type + amount + timestamp + key
                    );

                    const tokenData = { serial_id, user_id, meter_id, token_type, amount, timestamp, ciphertext };

                    try {
                        const {data} = await meter.generateTokenWater(tokenData);
                        
                        response.tokenData = data.tokenData;
                        response.meterType = this.meterType;
                        response.meterNumber = this.meterNumber;
                        response.amount = this.totalAmount;
                                                
                        const storeTransaction = await transaction.storeTransaction(response);

                        
                        try {
                            this.smsMessageData.tokenData= JSON.stringify(data.tokenData.result)
                            this.smsMessageData.phoneNumber = this.$store.state.userPhoneNumber
                             this.sendSMS(this.smsMessageData);
                            
                        } catch (error) {
                            this.flashMessage.error({
                            title: 'SMS not sent for '+this.$store.state.userPhoneNumber,
                            message: 'An admin has been notified about this issue! Please ensure your Number is correct or contact admin',
                            time: 8000,
                            icon: '../../assets/static/img/water_drop.svg'
                        });
                            
                        }
                       

                        this.validateFirstStepCheckWater = false;
                        this.loadTransactions();
                        
                    } catch (error) {
                        this.flashMessage.error({
                            title: 'Internet or Proxy Service Error',
                            message: 'An Email has been dispatched on this error! Please check your ISP or turn off proxy',
                            time: 8000,
                            icon: '../../assets/static/img/water_drop.svg'
                        });
                    }
                }else if (this.meterType === 'electricity') {
                    const tokenData = { 
                        transactionId: this.electricityMeterOwnerInfo.transactionId, 
                        amount: this.totalAmountToPay,
                        access_token: this.electricityMeterOwnerInfo.access_token
                    };

                    try {
                        const {data} = await meter.generateTokenElectricity(tokenData);
                        
                        response.tokenData = {
                            result: data.tokenData.data.token,
                            transaction: data.tokenData.data.transactionId
                            

                        };
                        //preparing SMS
                        this.smsMessageData.tokenData=  JSON.stringify(data.tokenData.data.token)//data.tokenData.data.token
                        this.smsMessageData.phoneNumber= this.$store.state.userPhoneNumber
                        console.log("Preparing SMS data "+this.smsMessageData.tokenData)

                        return 

                        response.transactionType = this.electricityMeterOwnerInfo.meterType;
                        response.discoType = this.electricityMeterOwnerInfo.discoType;
                        response.meterType = this.meterType;
                        response.meterNumber = this.meterNumber;
                        response.amount = this.totalAmount;
                        const storeTransaction = await transaction.storeTransaction(response);

                        this.validateFirstStepCheckElectricity = false;
                        this.loadTransactions();
                        

                    } catch (error) {
                        console.log(error)
                        this.flashMessage.error({
                            title: 'Transaction Failed',
                            message: 'Some error occured, Please reload the page and try again!',
                            time: 5000,
                            icon: '../../assets/static/img/water_drop.svg'
                        });
                    }
                }
                
                this.generateReference();
            },
            close: function(){
                // console.log("Payment closed")
            },
            sendSMS:async function(){
                try {
                    //console.log("In try Statement")
                    
                    await sendSMS.sendSMS(this.smsMessageData);
                } catch (error) {
                    console.log(error)
                        this.flashMessage.error({
                            title: 'SMS Not sent',
                            message: 'Some error occured, contact admin!',
                            time: 5000,
                            icon: '../../assets/static/img/water_drop.svg'
                        });
                    
                }
            },
            loadTransactions: async function() {
                this.selectedMeter = '';
                this.moreExists = true;
                try {
                    const transactions = await transaction.loadTransactions();
                    this.meterTransactions = transactions.data.meterTransactions;    
                    
                    var keys = Object.keys(this.meterTransactions.transactions);
                    var last = keys[keys.length-1];
                    this.lastTransactionDate = this.meterTransactions.transactions[last].createdAt;
                    
                    
                    
                } catch (error) {
                    this.flashMessage.error({
                        title: 'Error fetching data',
                        message: 'Some error occured during fetching your data please refresh the page!',
                        time: 5000,
                        icon: '/img/user-dummy.52069854.jpg'
                    });
                }
            },
            loadMeterTransactions: async function(meterId) {
                const request = {
                    meterId: meterId
                };
                try {
                    const transactions = await transaction.loadMeterTransactions(request);
                    this.meterTransactions = transactions.data.meterTransactions;    

                    var keys = Object.keys(this.meterTransactions.transactions);
                    var last = keys[keys.length-1];
                    this.lastTransactionDate = this.meterTransactions.transactions[last].createdAt;
                    this.selectedMeter = meterId; 
                    this.moreExists = true;
                } catch (error) {
                    this.flashMessage.error({
                        title: 'Error fetching data',
                        message: 'Some error occured during fetching your data please refresh the page!',
                        time: 5000,
                        icon: '/img/user-dummy.52069854.jpg'
                    });
                }
            },
            loadMoreTransactions: async function() {
                const request = {
                    lastTransactionDate: this.lastTransactionDate
                };
                try {
                    const transactions = await transaction.loadMoreTransactions(request);
                    if (transactions.data.moreTransactions.length > 0) {
                        transactions.data.moreTransactions.forEach(transaction => {
                            this.meterTransactions.transactions.push(transaction);
                        });

                        var keys = Object.keys(this.meterTransactions.transactions);
                        var last = keys[keys.length-1];
                        this.lastTransactionDate = this.meterTransactions.transactions[last].createdAt;

                        this.moreExists = true;
                    } else {
                        this.moreExists = false;
                    }
                } catch (error) {
                    this.flashMessage.error({
                        title: 'Error fetching data',
                        message: 'Some error occured during fetching your data please refresh the page!',
                        time: 5000,
                        icon: '/img/user-dummy.52069854.jpg'
                    });
                }
            },
            loadMeterMoreTransactions: async function() {
                const request = {
                    meterId: this.selectedMeter,
                    lastTransactionDate: this.lastTransactionDate
                };
                try {
                    const transactions = await transaction.loadMeterMoreTransactions(request);
                    if (transactions.data.moreTransactions.length > 0) {
                        transactions.data.moreTransactions.forEach(transaction => {
                            this.meterTransactions.transactions.push(transaction);
                        });

                        var keys = Object.keys(this.meterTransactions.transactions);
                        var last = keys[keys.length-1];
                        this.lastTransactionDate = this.meterTransactions.transactions[last].createdAt;

                        this.moreExists = true;
                    } else {
                        this.moreExists = false;
                    }
                } catch (error) {
                    this.flashMessage.error({
                        title: 'Error fetching data',
                        message: 'Some error occured during fetching your data please refresh the page!',
                        time: 5000,
                        icon: '/img/user-dummy.52069854.jpg'
                    });
                }
            },
            commingSoon() {
                alert('Common Soon!');
            }
        }
    };
</script>
